$rinsta-color-b: #262626;
$rinsta-color-gray: #262626ab;

.rinsta-header {
  display: flex;

  @media (max-width: 493px) and (min-width: 300px) {
    margin: 0;
  }

  @media (max-width: 299px) and (min-width: 200px) {
    margin: 0;
  }

  .rinsta-logo {
    @media (max-width: 493px) and (min-width: 300px) {
      margin: 0;
    }

    @media (max-width: 299px) and (min-width: 200px) {
      margin: 0;
    }

    img {
      width: 150px;
      height: 150px;
      border-radius: 50%;
      border: 1px solid #ffffff;
      box-shadow: 1px 1px 10px 0px rgba(85, 84, 88, 0.9);
      transition: transform 0.3s ease-in-out;

      &:hover {
        transform: scale(1.05);
      }
    }
  }
}
