$rinsta-color-b: #262626;
$rinsta-color-gray: #262626ab;

.rinsta-header {
  display: flex;

  .rinsta-header__box {
    .rinsta-username {
      display: block;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      color: $rinsta-color-gray;
      font-weight: 100;
      font-size: 36px;
      margin: 0;

      @media (max-width: 493px) and (min-width: 300px) {
        font-size: 30px;
      }

      @media (max-width: 299px) and (min-width: 200px) {
        font-size: 30px;
      }
    }
  }
}
