
.rinsta-wrapper-buton {
    display: flex;
    justify-content: center;

    .rinsta-button__inc {
        cursor: pointer;
        display: block;
        font-size: 14px;
        color: #fff;
        background: #3897f0;
        border: 0;
        font-weight: 600;
        text-align: center;
        border-radius: 0.40em;
        transition: transform 0.3s ease-in-out, opacity 0.5s ease-in-out;
        text-decoration: none;
        padding: 10px 30px;
        outline: none;

        &:hover {
            transform: translateY(3px);
            opacity: 0.8;
        }
    }
}

