$rinsta-color-b: #262626;
$rinsta-color-gray: #262626ab;

.rinsta-header {
  display: flex;

  @media (max-width: 991px) and (min-width: 790px) {
    flex-direction: column;
  }

  @media (max-width: 789px) and (min-width: 600px) {
    flex-direction: column;
  }

  @media (max-width: 599px) and (min-width: 200px) {
    flex-direction: column;
  }

  @media (max-width: 493px) and (min-width: 300px) {
    flex-direction: column;
  }

  @media (max-width: 299px) and (min-width: 200px) {
    flex-direction: column;
  }

  .rinsta-article {
    @media (max-width: 493px) and (min-width: 300px) {
      margin-right: 0;
    }
  
    @media (max-width: 299px) and (min-width: 200px) {
      margin-right: 0;
    }
  }

  .rinsta-header__box {
    .rinsta-username {
      display: block;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      color: $rinsta-color-gray;
      font-weight: 100;
      font-size: 36px;
      margin: 0;
    }

    .rinsta-edge_liked_by {
      &__text {
        color: $rinsta-color-b;
      }
    }

    .rinsta-location {
      &__name {
        color: $rinsta-color-b;
      }
    }

    .rinsta-edge_media_to_caption {
    }
  }
}
