
$img-box-293: 293px;

.rinsta-box-post {
  position: relative;
  overflow: hidden;

  &:hover .rinsta-shadow-box {
    opacity: 1;
    visibility: visible;
  }

  &:hover .rinsta-display_url {
    transform: scale(1.1);
  }
}

.rinsta-shadow-box {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      width: 20px;
      height: 20px;
    }

    .count {
      color: #fff;
    }

    span {
      display: block;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      width: 40px;
      height: 20px;
      color: #fff;
    }
  }
}

.rinsta-display_url {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: $img-box-293;
  height: $img-box-293;
  transition: transform 0.3s ease-in-out;

  @media (max-width: 992px) and (min-width: 790px) {
    width: 240px;
    height: 240px;
  }

  @media (max-width: 789px) and (min-width: 600px) {
    width: 180px;
    height: 180px;
  }

  @media (max-width: 599px) and (min-width: 494px) {
    width: 220px;
    height: 220px;
  }

  @media (max-width: 493px) and (min-width: 300px) {
    width: 293px;
    height: 293px;
  }

  @media (max-width: 299px) and (min-width: 200px) {
    width: 185px;
    height: 185px;
  }
}
