$rinsta-color-b: #262626;
$rinsta-color-gray: #262626ab;

.rinsta-edgecounters {
  display: flex;

  @media (max-width: 493px) and (min-width: 300px) {
    flex-direction: column;
  }

  @media (max-width: 299px) and (min-width: 200px) {
    flex-direction: column;
  }

  &__item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: $rinsta-color-gray;
    font-weight: 100;
    font-size: 16px;
    text-transform: lowercase;

    &_count {
      font-size: 18px;
      font-weight: 700;
      color: $rinsta-color-b;
    }
  }
}
