$rinsta-color-b: #262626;
$rinsta-color-gray: #262626ab;
$rinsta-color-blue: #3897f0;

.rinsta-header {
    display: flex;
  
    .rinsta-full-name {
        color: $rinsta-color-gray;
        font-weight: 400;
        margin: 0;
    }

    .rinsta-biography {
        color: $rinsta-color-b;
        font-weight: 400;
        margin: 0;
    }

    .rinsta-external-url__a {
        color: $rinsta-color-blue;
        text-decoration: none;
        font-weight: 700;
    }
  }