body {
  margin: 0;
  height: 9000px;
}

.App {
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Helvetica,Arial,sans-serif;
  display: flex;
  justify-content: center;
  flex-direction: column;
  box-sizing: content-box;
  padding: 0px 20px 0;
  max-width: 960px;
  margin: 0 auto;

  @media (max-width: 991px) and (min-width: 790px) {
    max-width: 750px;
  }
  @media (max-width: 789px) and (min-width: 600px) {
    max-width: 580px;
  }

  @media (max-width: 599px) and (min-width: 200px) {
    max-width: 460px;
  }

  @media (max-width: 493px) and (min-width: 300px) {

  }

  @media (max-width: 299px) and (min-width: 200px) {

  }

}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}