
@keyframes logoInst {

    30% {
        transform: scale(1);
        transition: transform 3s ease-in-out;
    }

    60% {
        transform: scale(1.6);
        transition: transform 3s ease-in-out;
    }

    90% {
        transform: scale(1);
        transition: transform 3s ease-in-out;
    }
}

.rinsta-loader-box {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 300px;

    img {
        width: 60px;
        height: 60px;
        animation: 5s infinite logoInst;
    }
}